import React from 'react';
import PropTypes from 'prop-types';

const GoodsList = ({ allGoods }) => (
  <ul>
    {allGoods.map(good => (
      <li>
        {good}
      </li>
    ))}
  </ul>
);

GoodsList.propTypes = {
  allGoods: PropTypes.shape({
    good: PropTypes.string,
    map: PropTypes.func,
  }).isRequired,
};

export default GoodsList;
